.card-container {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	justify-content: center;
	height: 90vh;
	align-content: space-evenly;
	justify-items: center;
	overflow: auto;
}

@media only screen and (max-width: 490px) {
	.card-container {
		grid-template-columns: 1fr 1fr;
	}
}
